*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: monospace, Verdana, Geneva, Tahoma, sans-serif;
}

header{
    display: flex;
    align-items: center;
    height: 65px;
    background: linear-gradient(90deg,#672280 1.18%, #a626d3 100%);
    color: floralwhite;
    padding: 15px 20px;
}
header > .logo{
    height: 100%;
    margin-right: 12px;
}
main{
    padding: 36px 120px;
}
main > .form-container{
    display: grid;
    grid-template-rows: 40px 40px;
    grid-template-columns: 1fr 1fr; 
    gap: 16px;
    justify-content: center;
}
.form-container > input{
    border:  2px solid #d2d3d4;
    border-radius: 8px;
    font-size: 1.2rem;
}
.form-container > button {
    grid-column: 1/-1;
    cursor: pointer;
    margin: 0 100px;
    border: none;
    border-radius: 8px;
    background: #666768;
    color: ghostwhite;
    font-weight: 600;
    letter-spacing: 1px;
    font-size: 1rem;
}

.meme img{
    width: 100%;
    height: 100%;
}
.meme {
    position: relative;
    margin: 2rem auto;
    max-width: 720px;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}


@media (max-width: 786px){
    main{
        padding: 18px;
    }
     main > .form-container{
        grid-template-columns: 1fr;
        grid-template-rows: 40px 40px 40px; 
        /* gap:16px; */
        row-gap: 28px;
    }
    .form-container > button {
        margin: 0 60px;
        font-size: 0.7rem;
        font-weight: 800;
        padding:  0 5px;
    }

}

@media (max-width:992px) {
    main{
        padding: 48px;
    }
    .form-container{
        grid-template-columns: 1fr 1fr;
        grid-template-rows:  40px 40px;    
    }
    .form-container >button{
        margin: 0 80px;
    }
}